    '–ü–∞—Ä–µ–Ω—å –ø–æ–ª—Ç–æ—Ä—ã (–¥–≤–µ) –º–∏–Ω—É—Ç—ã –ª–∞—Å–∫–∞–µ—Ç, —Ü–µ–ª—É–µ—Ç —Å—Ç—É–ø–Ω–∏ –¥–µ–≤—É—à–∫–∏',
